<template>
  <div class="layout" v-cloak>
    <transition name="fade">
      <div class="home-title" v-if="titleShow">
        <div class="title-page"><span>My</span><span>Blog</span></div>
        <div class="title-author">Dao Dao</div>
        <div class="title-text">Better an empty purse than an empty head</div>
      </div>
    </transition>
    <layout-container>
      <layout-nav slot="aside" :is-show="sideShow" :widen="sideWiden" />
      <el-main class="main-layout" v-if="mainShow">
        <router-view />
      </el-main>
    </layout-container>
  </div>
</template>

<script>
// @ is an alias to /src
import LayoutNav from "./component/LayoutNav.vue";
import LayoutContainer from "./component/LayoutContainer.vue";

export default {
  name: "Layout",
  components: {
    LayoutNav,
    LayoutContainer
  },
  data() {
    return {
      sideShow: false,
      mainShow: false,
      titleShow: false,
      sideWiden: false
    }
  },
  watch: {
    $route(to, from) {
      console.log('new, old', to, from)
      this.sideWiden = this.titleShow = this.$route.name === "Home";
    }
  },
  mounted() {
    this.sideShow = true;
    setTimeout(()=> {
      this.mainShow = true;
    }, 600)
    this.sideWiden = this.titleShow = this.$route.name === "Home";
    // console.log(this.$route.name)
  },
};
</script>
<style lang="scss" scoped>
  .layout {
    overflow: hidden;
  }
  .home-title {
    z-index: 99;
    position: fixed;
    left: 16vw;
    top: 48vh;
    transform: translateY(-50%);
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    .title-page {
      font-size: 10vh;
      color: #BCFB25;
      text-shadow: #00b4aa .5vh .5vh 0;
      // text-shadow: #75BAD2 4vh 0 0, #75BAD2 0 4vh 0, #75BAD2 -4vh 0 0, #75BAD2 0 -4vh 0;
      span + span {
        margin-left: 2vw;
        color: #fff;
      }
    }
    .title-author {
      font-size: 6vh;
      color: #fff;
      text-shadow: #00b4aa .5vh .5vh 0;
      margin-top: 1vh;
    }
    .title-text {
      position: relative;
      font-size: 3vh;
      color: #fff;
      text-shadow: #00b4aa .2vh .2vh 0;
      margin-top: 2vh;
      &::after {
        content: '';
        position: absolute;
        bottom: -.4vh;
        left: 0;
        height: 1px;
        width: 65%;
        background: #fff;
      }
    }
  }
  .main-layout {
    padding: 0;
  }


  .fade-enter-active {
    transition: all 1s ease;
  }
  .fade-leave-active {
    transition: all .5s ease;
  }
  .fade-enter, .fade-leave-to {
    transform: translateX(10vw);
    opacity: 0;
  }

  [v-cloak] {
    display: none;
  }
</style>