<template>
  <!-- <transition name="top-in"> -->
    <el-aside :width="widen? '30vw' : '20vw'" :class="['aside-layout', widen?'is-widened': 'not-widened']" v-if="isShow">
      <i class="iconfont icon-back" @click="handleBack" v-if="backShow"></i>
      <i class="iconfont icon-home" @click="handleBack" v-if="backShow"></i>
    </el-aside>
  <!-- </transition> -->
</template>

<script>
export default {
  name: "PageNav",
  props: {
    isShow: {
      type: Boolean,
      default() {
        return false
      }
    },
    widen: {
      type: Boolean,
      required: true,
    }
  },
  data() {
    return {
      backShow: false
    }
  },
  watch: {
    $route(to, from) {
      console.log('new, old', to, from)
      this.backShow = this.$route.name !== "Home";
    }
  },
  mounted() {
    this.backShow =  this.$route.name !== "Home"
  },
  methods: {
    handleBack() {
      this.$router.go(-1)
    },
    handleHome() {
      this.$router.push('Home')
    }
  },
};
</script>

<style scoped lang="scss">
.aside-layout {
  position: relative;
  z-index: 1;
  padding: 2vh 2vw;
  background: linear-gradient(45deg, #75BAD2, #8ADBF8);
}

.iconfont {
  position: absolute;
  color: #fff;
  font-size: 3vh;
  cursor: pointer;
  &:hover {
    opacity: .6;
  }
  &.icon-back {
    left: 2vw;
    top: 2vh;
  }
  &.icon-home {
    left: 4vw;
    top: 2vh;
  }
}

// .top-in-enter-active {
//   animation: top-in .8s;
// }
// .top-in-leave-active {
//   animation: top-in .8s reverse;
// }
.is-widened {
  animation: widen 1s ease;
}
.not-widened {
  animation: nowiden 1s ease;
}

// @keyframes top-in {
//   from {
//     transform: translateY(-100%);
//   }
//   to {
//     transform: translateY(0);
//   }
// }
@keyframes widen {
  from {
    width: 20vw;
  }
  to {
    width: 30vw;
  }
}
@keyframes nowiden {
  from {
    width: 30vw;
  }
  to {
    width: 20vw;
  }
}
</style>
